/* Yweet */

.ynweetEdit .formBtn {
    cursor: pointer;
    margin-top: 15px;
    margin-bottom: 15px;
  }
  
  .yweet {
    margin-bottom: 30px;
    background-color: white;
    width: 100%;
    max-width: 320px;
    padding: 10px;
    border-radius: 10px;
    position: relative;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    color: rgba(0, 0, 0, 0.8);
  }
  
  .yweet h4 {
    font-size: 14px;
  }
  
  .yweet img {
    right: -10px;
    top: 20px;
    position: absolute;
    border-radius: 50%;
    width: 50px;
    height: 50px;
    margin-top: 10px;
  }
  
  .yweet__actions {
    position: absolute;
    right: 10px;
    top: 10px;
  }
  
  .yweet__actions span {
    cursor: pointer;
  }
  
  .yweet__actions span:first-child {
    margin-right: 10px;
  }
  
/* Nwitter Factory */

.factoryForm {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    width: 100%;
    margin-top: 30px;
  }
  
  .factoryInput__container {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    position: relative;
    margin-bottom: 20px;
    width: 100%;
  }
  
  .factoryInput__input {
    -webkit-flex-grow: 1;
            flex-grow: 1;
    height: 40px;
    padding: 0px 20px;
    color: white;
    border: 1px solid #04aaff;
    border-radius: 20px;
    font-weight: 500;
    font-size: 12px;
  }
  
  .factoryInput__arrow {
    position: absolute;
