    padding: 0px 0px;
    text-align: center;
    border-radius: 20px;
    color: white;
  }
  
  .factoryInput__label {
    color: #04aaff;
    cursor: pointer;
  }
  
  .factoryInput__label span {
    margin-right: 10px;
    font-size: 12px;
  }
  
  .factoryForm__attachment {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
  }
  
  .factoryForm__attachment img {
    height: 80px;
    width: 80px;
    border-radius: 40px;
  }
  
  .factoryForm__clear {
    color: #04aaff;
    cursor: pointer;
    text-align: center;
  }
  
  .factoryForm__clear span {
    margin-right: 10px;
    font-size: 12px;
  }
